export default{
	data(){
		return {
			items:"",
			TabCur: 0,
			scrollLeft: 0,
			navlist:["推荐","宝贝","新品"]//"视频"
		}
	},
	methods:{
		tabSelect(e) {
			let {id,items} = e.currentTarget.dataset
			this.items = items
			
			// console.log(e.currentTarget.dataset)
			this.TabCur = id;
			this.scrollLeft = (e.currentTarget.dataset.id - 1) * 60
			this.$emit("getChildnav",this.items)
		}
	},
	
}
